.App {
  width: 100vw;
}

.App img {
  margin: auto;
}

.App-logo {
  margin: auto;
  position: absolute;
  right: 15px;
  top: 15px;
  margin: auto;
}

.App-header {
    background-color: #a7b4b5;
    height: 150px;
    padding: 20px;
    color: white;
}

.title {
    float: right;
    padding: 20px 0 0;
    display: block;
    font-size: 20px;
    color: #464545;
    font-weight: 700;
    margin-right: 80px;
}

.App-intro {
    font-size: large;
}

.inputSearch {
    border-radius: 2em / 5em;
    width: 250px;
    height: 20px;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}



